"use client";

import { useState } from "react";
import { LayoutWrapper } from "./LayoutWrapper";
import { HeroSection } from "./HeroSection";
import { StepsForm } from "./StepsForm";
import { SubmitButton } from "./SubmitButton";
import { projectSteps } from "./data/projectSteps";

export default function FinalStartup() {
  const [formData, setFormData] = useState<Record<number, string>>({});
  const [isSubmitting, setIsSubmitting] = useState(false);

  const handleFormChange = (stepNumber: number, value: string) => {
    setFormData((prev) => ({ ...prev, [stepNumber]: value }));
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    setIsSubmitting(true);
    // Here you would typically handle form submission
    console.log("Form data:", formData);
    // Simulate API call
    setTimeout(() => {
      setIsSubmitting(false);
      alert("Submission successful!");
    }, 1500);
  };

  return (
    <LayoutWrapper className="py-12">
      <HeroSection
        title="Project Startup Submission"
        subtitle="Complete all 9 steps to submit your project for review"
      />
      
      <StepsForm
        steps={projectSteps}
        formData={formData}
        onFormChange={handleFormChange}
        onSubmit={handleSubmit}
      />
      
      <div className="mt-10 flex justify-end">
        <SubmitButton isLoading={isSubmitting} />
      </div>
    </LayoutWrapper>
  );
}