import { FormEvent } from "react";
import { StepInput } from "./StepInput";
import { ProjectStep } from "./data/projectSteps";

interface StepsFormProps {
  steps: ProjectStep[];
  formData: Record<number, string>;
  onFormChange: (stepNumber: number, value: string) => void;
  onSubmit: (e: FormEvent) => void;
}

export function StepsForm({
  steps,
  formData,
  onFormChange,
  onSubmit,
}: StepsFormProps) {
  return (
    <form onSubmit={onSubmit} className="mt-10">
      <div className="space-y-10">
        {steps.map((step) => (
          <StepInput
            key={step.number}
            stepNumber={step.number}
            title={step.title}
            placeholder={step.placeholder}
            value={formData[step.number] || ""}
            onChange={(value) => onFormChange(step.number, value)}
            Icon={step.Icon}
          />
        ))}
      </div>
    </form>
  );
}